.flexWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
  
.left {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-order: 1;
        order: 1;
    padding: 20px;
} 

.right {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-order: 2;
        order: 2;
} 

#mapForm {
    margin: 20px auto;
    position: relative;
    height: 30px;
}

input {
      height: 100%;
      font-size: 1.4em;
}

input[type=text] {
      width: 50%;
      position: absolute;
      top: 0;
      left: 0;
      border: 2px solid rgb(0, 0, 0);
}

input[type=submit] {
      width: 10%;
      position: absolute;
      left: 570px;
      top: 0;
      border: 2px solid rgb(0, 0, 0);
      background: rgb(135, 138, 180);
}
  
  



